.sale{
  position: relative;
  &__bg{
    position: absolute;
    top: -5.5em;
    left: 0;
    width: 100%;
    pointer-events: none;
  }
  &__bg2{
    display: none;
  }
  &__inner{
    min-height: 30em;
    background-image: url(../img/sale-bg.webp);
    background-size: cover;
    background-position: center;
    border-radius: 1.5em;
    color: $white;
    padding: 1.875em 4em 2.875em;
    position: relative;
  }
  &__top{
    margin: -2em 0 2em;
    height: 8.75em;
  }
  &__title{
    font-size: 12.5em;
    background: linear-gradient(90deg, #FFF 12.33%, rgba(255, 255, 255, 0.64) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  &__nav{
    display: flex;
    margin-top: 2.125em;
  }
  &__img{
    position: absolute;
    right: .875em;
    bottom: 1.1875em;
    width: 35.5em;
  }
}

.sale-gift{
  margin-top: .75em;
  margin-left: -1.5em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-transform: uppercase;
  &__row{
    padding: .75em 1.5em;
    background-color: $krasnyy;
    border-radius: 2.625em 2em 2em 0;
  }
  &__row2{
    margin-top: -.5em;
    padding: .0625em 2.91em .56em 1.5em;
    background-color: $krasnyy;
    border-radius: 0 0 2.625em 2.625em;
  }
  &__title{
    font-size: 4em;
    font-weight: 700;
  }
}

.catalog{
  &__grid{
    margin-top: 2.625em;
  }
}

.product-card{
  border-radius: 1.5em;
  background-color: $white;
  display: flex;
  flex-direction: column;
  &__img{
    border-radius: 1.5em 1.5em 0 0;
    @include imgCover(98%);
  }
  &__content{
    padding: 1.625em 2em 2em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.875em;
    display: flex;
  }
  &__btn{
    flex-grow: 1;
    margin-left: .5em;
    padding: 0;
  }
}

.cart-btn{
  ._view2{
    display: none;
  }
  &._added{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .sale{
    &__bg{
      top: -2.875em;
    }
    &__inner{
      border-radius: 1em;
      padding: 1.125em 1.75em 1.5em;
      min-height: 16em;
    }
    &__top{
      height: unset;
      margin: 0;
    }
    &__title{
      font-size: 5em;
    }
    &__img{
      width: 19em;
    }
  }

  .sale-gift{
    margin-top: .44em;
    margin-left: -.875em;
    &__row{
      padding: .5em .875em;
      border-radius: 1.62em 1.23em 1.23em 0;
    }
    &__row2{
      margin-top: -.3125em;
      padding: .0625em 1.19em .31em .875em;
      border-radius: 0 0 1.62em 1.62em;
    }
    &__title{
      font-size: 2.5em;
      line-height: 99%;
    }
  }

  .catalog{
    &__grid{
      margin-top: 2em;
    }
  }

  .product-card{
    border-radius: 1em;
    &__img{
      border-radius: 1em 1em 0 0;
    }
    &__content{
      padding: 1em 1.5em;
    }
    &__bottom{
      padding-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .sale{
    &__bg{
      display: none;
    }
    &__bg2{
      display: block;
      position: absolute;
      top: -2.875em;
      left: 0;
      width: 100%;
      pointer-events: none;
    }
    &__inner{
      background-image: url(../img/sale-bg2.webp);
      padding-right: .75em;
    }
    &__top{
      display: none;
    }
    &__img{
      position: static;
      margin-top: 1.19em;
      width: 100%;
    }
    &__nav{
      margin-top: 0;
      justify-content: center;
    }
    &__link{
      padding: 0 3em;
      span{
        font-size: 1.125em;
      }
    }
  }
}
