.pin{
  font-size: .75em;
  padding: 0 .125em;
  height: 1.33em;
  min-width: 1.33em;
  border-radius: .75em;
  color: $white;
  background-color: $krasnyy;
  @include center;
}

.header-action{
  display: flex;
  margin-left: auto;
  gap: 2em;
  .pin{
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: .25em;
  }
  &__btn{
    position: relative;
    @include center;
    flex-direction: column;
    img{
      width: 1.5em;
      margin-bottom: .5em;
    }
    &-title{
      font-size: .875em;
      line-height: 143%;
      font-weight: 600;
    }
  }
}

.menu{
  margin-left: 5em;
  &__close{
    display: none;
  }
}

.nav{
  font-weight: 600;
  ul{
    display: flex;
    gap: 4em;
  }
  a{
    font-size: 1.125em;
    line-height: 111%;
  }
}

.header-gift{
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 100%;
  margin-bottom: .75em;
  right: 19.25em;
  img{
    width: 3.5em;
    margin-right: .5em;
    display: inline-block;
    transform-origin: 50% 50%;
    animation: iconShake 5s infinite;
  }
}

@keyframes iconShake {
  /* 08% ,    100% */
  0%   { transform: translateX(0) rotate(0); }
  1%   { transform: translateX(-2px) rotate(-6deg); }
  2%   { transform: translateX(2px)  rotate(6deg); }
  3%   { transform: translateX(-2px) rotate(-6deg); }
  4%   { transform: translateX(2px)  rotate(6deg); }
  5%   { transform: translateX(-1px) rotate(-3deg); }
  6%   { transform: translateX(1px)  rotate(3deg); }
  7%   { transform: translateX(0) rotate(0); }
  100% { transform: translateX(0) rotate(0); } /*  */
}

.header-banner{
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .header{
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-action{
    gap: 1.25em;
    &__btn-title{
      display: none;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    margin: 0;
    background-color: $white;
    z-index: 100;
    padding: 3.5em 0 1em;
    overflow-y: auto;
    transition: all .2s;
    opacity: 0;
    pointer-events: none;
    &__close{
      position: absolute;
      top: 1em;
      right: 1em;
      width: 2.5em;
      @include center;
      img{
        width: 100%;
      }
    }
  }

  .nav{
    ul{
      display: block;
    }
    a{
      border-bottom: 1px solid rgba(233, 211, 189, 0.40);
      display: flex;
      align-items: center;
      font-size: 1em;
      line-height: 125%;
      padding: 1em 1.5em;
    }
  }

  .header-gift{
    position: static;
    padding: .75em 1.5em;
    img{
      width: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .header-banner{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}
