.m-section{
  padding: 3.75em 0;
  &._first{
    padding-top: 0;
  }
}

.faq{
  &__inner{
    display: grid;
    grid-template-columns: 39.5em calc(100% - 46.5em);
    gap: 7em;
    position: relative;
  }
  &__items{
    @include grid100gap(2.5em);
    margin-top: 2.625em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1.5em;
  }
  &__bg{
    position: absolute;
    top: -9.5em;
    left: -1.5em;
    width: 56.875em;
    pointer-events: none;
  }
}

.qa-card{
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
    path{
      stroke: $zelenyy;
    }
    .qa-card__title{
      color: $zelenyy;
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    transition: all .3s;
  }
  &__btn{
    margin-left: 1em;
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    svg{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    margin-top: 1.1875em;
    padding-right: 2em;
    display: none;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1.25em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
    padding-right: 1.25em;
  }
  &__bottom{
    margin-top: 2.625em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .swiper-pagination{
    width: unset;
    position: static;
  }
  .swiper-button{
    position: static;
    &.swiper-button-disabled{
      opacity: .4;
    }
  }
}

.swiper-pagination{
  display: flex;
  align-items: center;
  gap: .8125em;
  &-bullet{
    width: 1em;
    height: 1em;
    background-color: $svetlo-seryy;
    opacity: 1;
    margin: 0 !important;
    border-radius: .5em;
    &-active{
      width: 3em;
      background-color: $korichnevyy;
    }
  }
}

.swiper-nav{
  display: flex;
  gap: 2em;
}

.swiper-button{
  width: 3em;
  height: 3em;
  @include center;
  margin: 0;
  svg,
  img{
    width: 100%;
  }
  &.swiper-button-disabled{
    pointer-events: unset;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2.5em 0;
  }

  .faq{
    &__inner{
      grid-template-columns: 22em calc(100% - 23em);
      gap: 1em;
    }
    &__items{
      gap: 2em;
      margin-top: 2em;
    }
    &__bg{
      display: none;
    }
  }

  .slider{
    &__bottom{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      display: none;
    }
  }
}
