.max-w600{
  max-width: 37.5em;
}

.feedback-slider{
  margin-top: 2.75em;
  .swiper-slide{
    width: 25%;
  }
}

.m-video{
  position: relative;
  &:after{
    content: '';
    @include centerAbsolute;
    z-index: 2;
    width: 4em;
    height: 4em;
    background-image: url(../img/play.svg);
    @include bgImgSize(100%);
  }
}

.feedback-card{
  &__img{
    @include imgCover(178%);
    border-radius: 1.5em;
  }
}

.advantages {
  &__inner {
    display: grid;
    grid-template-columns: 28.5em 31.5em;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 0 1.81em 0 2.5em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.75em));
    gap: 2.625em 5.5em;
  }
  &__bg{
    position: absolute;
    top: -2.75em;
    left: -2em;
    width: 45.3125em;
    z-index: -1;
  }
  &__bg2{
    display: none;
  }
}

.advantage-item{
  font-weight: 600;
  &__icon{
    height: 4em;
    display: block;
    margin-bottom: .75em;
  }
}


@media screen and (max-width: 992px){
  .feedback{
    overflow: hidden;
  }

  .feedback-slider{
    margin-top: 2em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 20.25em;
    }
  }

  .advantages {
    &__inner{
      grid-template-columns: calc(100% - 22.5em) 21.5em;
      gap: 1em;
      padding: 0;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__bg{
      display: none;
    }
    &__bg2{
      display: block;
      position: absolute;
      top: -5.875em;
      left: -3em;
      width: 26.25em;
      max-width: calc(100% + 4em);
    }
  }

  .advantage-item{
    &__icon{
      height: 3em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__inner {
      display: block;
      padding: 2.75em 0 0 11.1875em;
      min-height: 35em;
    }
    &__col{
      position: absolute;
      top: 0;
      right: 100%;
      margin-right: -11.1875em;
    }
    &__img{
      width: auto;
      height: 33em;
    }
    &__items{
      grid-template-columns: 100%;
      position: relative;
    }
    .advantage-item{
      &:last-child{
        position: absolute;
        right: 100%;
        margin-right: .875em;
        bottom: 0;
        width: 100%;
      }
    }
  }
}
