.product-info,
.product-info2{
  width: 21.125em;
  &__props{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
}

.prop-item{
  display: grid;
  grid-template-columns: 8em calc(100% - 9em);
  gap: 1em;
  align-items: flex-start;
  &__col{
    color: $svetlo-korichnevyy;
  }
  &__col2{
    font-weight: 500;
    @include grid100gap(.25em);
  }
  &._end{
    align-items: flex-end;
  }
}

.product-info2{
  margin-top: 2em;
  &__props{
    margin-top: 1em;
  }
}

.product-categories{
  width: 22.5em;
  margin-top: 2em;
  &__slider{

  }
  &__item{
    padding: 0 1.375em;
    height: 2.75em;
    border-radius: 6.25em;
    border: 1px solid $svetlo-seryy;
    display: flex;
    align-items: center;
    font-weight: 600;
    color: $korichnevyy;
    &:hover{
      color: $primary;
    }
    &._active{
      color: $korichnevyy;
      border-color: $primary;
      pointer-events: none;
    }
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    gap: .5em;
    margin-top: 1em;
  }
  @media screen and (min-width: 577px){
    .swiper-container{
      width: 100%;
    }
    .swiper-wrapper{
      transform: unset !important;
      flex-wrap: wrap;
      gap: .5em;
    }
    .swiper-slide{
      padding: 0;
      width: auto;
    }
  }
}

.reviews{
  overflow: hidden;
  &__tabs{
    display: flex;
    gap: .5em;
  }
  &__tab{
    min-width: 17em;
    padding: 0 1.875em;
    border-radius: 5em;
    border: 1px solid $svetlo-korichnevyy;
    height: 3.5em;
    @include center;
    color: $svetlo-korichnevyy;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      background-color: $svetlo-korichnevyy;
      color: $white;
    }
    &._active{
      background-color: $korichnevyy;
      color: $white;
    }
  }
  .tab-content{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .product-info,
  .product-info2{
    width: 100%;
    &__props{
      gap: .5em;
    }
  }

  .product-info2{
    margin-top: 1.5em;
    &__props {
      margin-top: .75em;
    }
  }

  .product-categories{
    width: 100%;
    margin-top: 1.5em;
    &__item{
      padding: 0 1.25em;
      height: 2.5em;
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .product-categories{
    order: -1;
    margin: 0 0 0 -1em;
    width: calc(100% + 2em);
    position: relative;
    &:after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2em;
      background-color: #F8F3ED;
    }
    &__title{
      display: none;
    }
    &__items{
      overflow-x: auto;
      flex-wrap: nowrap;

      padding: 0 1em 1em;
      margin: 0 0 1em;
      //&::-webkit-scrollbar {
      //  width: 0;
      //  height: 0;
      //}
    }
    &__item{
      flex-shrink: 0;
    }
    &__slider{
      margin: 0;
      .swiper-container{
        width: calc(100% + .25em);
      }
      .swiper-slide{
        padding-right: .25em;
        width: auto;
      }
    }
  }

  .reviews{
    &__tab{
      min-width: unset;
      flex-grow: 1;
      padding: 0;
    }
    .tab-content{
      margin-top: 1.5em;
    }
  }
}
