.order-modal{
  &__inner{
    width: 39em;
    max-width: 100%;
    margin: 0 auto;
  }
}

.order-step{
  &__items{
    margin-top: 2em;
  }
}

.order-item{
  display: grid;
  grid-template-columns: calc(100% - 13.75em) 12.5em;
  gap: .5em 1.25em;
  align-items: center;
  padding: 1em 0;
  border-bottom: 1px solid rgba(233, 211, 189, 0.24);
  &__col{
    display: grid;
    grid-template-columns: 5.5em calc(100% - 15em) 6.5em;
    gap: 1.5em;
    align-items: center;
  }
  &__col2{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .75em;
  }
  .cartcalc{
    width: 100%;
    .ccalc-minus{
      left: 1em;
    }
    .ccalc-plus{
      right: 1em;
    }
  }
  &__price{
    text-align: right;
    flex-grow: 1;
  }
  &__remove{
    width: 2.625em;
    height: 2.625em;
    @include center;
    background-color: transparent;
    margin-left: 2.5em;
    img{
      width: 100%;
    }
  }
}

.order-sum{
  margin-top: 1.5em;
  &__price{
    text-align: right;
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
    justify-content: flex-end;
    gap: .5em;
  }
}

.order-suggest{
  margin-top: 2em;
  .fg{
    max-width: 26.5em;
    margin-top: 1em;
  }
}

.order-products{
  margin: 2em 0;
}

.products-slider{
  .swiper-container{
    width: calc(100% + .5em);
  }
  .swiper-slide{
    width: calc(100% / 6);
    padding-right: .5em;
  }
}

.product-card2{
  &__top{
    position: relative;
  }
  &__btn{
    position: absolute;
    right: .5em;
    bottom: .5em;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    background-color: $primary;
    background-image: url(../img/plus2.svg), url(../img/cart.svg);
    @include bgImgSize(100%);
    transition: all 0s;
    background-size: 1em auto, 0 auto;
    &._added{
      background-size: 0 auto, 1em auto;
      background-color: $white;
    }
  }
  &__img{
    margin-bottom: .1875em;
    border-radius: .75em;
    @include imgCover(129%);
  }
}


@media screen and (max-width: 992px){
  .order-products{
    width: calc(100% + 2em);
    overflow: hidden;
    padding: 0 1em;
    margin-left: -1em;
  }

  .products-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 6.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-modal{
    .order-item__price{
      left: 12.5em;
    }
  }

  .order-step{
    &__items{
      margin-top: 1em;
    }
  }

  .order-item{
    display: block;
    position: relative;
    &__col{
      grid-template-columns: 100%;
      gap: .25em;
      position: relative;
      padding-left: 5em;
      padding-right: 3em;
    }
    &__img{
      width: 4em;
      height: 4em;
      position: absolute;
      top: 0;
      left: 0;
      padding: 0;
    }
    &__price{
      text-align: left;
      position: absolute;
      left: 13.5em;
      bottom: 2.25em;
      transform: translateY(50%);
    }
    .cartcalc{
      width: 6.5em;
      height: 2.5em;
      input{
        font-size: .875em;
      }
    }
    &__remove{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      width: 2em;
      height: 2em;
    }
  }

  .order-sum{
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .5em;
      &:last-child{
        margin: 0;
      }
    }
  }
}
