.order{
  &__inner{
    display: grid;
    grid-template-columns: 34em 38.625em;
    justify-content: space-between;
    gap: 2em;
  }
  .order-sum__btn{
    min-width: 15.5em;
  }
  &__groups{
    margin-top: 2em;
    @include grid100gap(.75em);
  }
}

.order-more{
  margin-top: 4em;
  .order-products{
    margin: .75em 0 0;
  }
}

.order-group{
  padding: 2em 2.5em;
  background-color: $white;
  border-radius: 1.5em;
  &__fields{
    margin-top: 1.25em;
    @include grid100gap(1em);
    max-width: 25em;
  }
  &__radios{
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
}

.delivery-item{
  position: relative;
  input[type="radio"]{
    height: 1.5em;
    position: absolute;
    opacity: 0;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked~.delivery-item__bottom{
      display: block;
    }
    &:checked~.m-radio label{
      &:before{
        border: none;
        background-size: 100% auto;
      }
    }
  }
  &__bottom{
    margin-top: 1em;
    display: none;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .5em));
    gap: .75em;
  }
}


@media screen and (max-width: 992px){
  .order {
    &__inner {
      grid-template-columns: 100%;
    }
    &__groups{
      margin-top: 1.25em;
    }
  }

  .order-more{
    margin-top: 2em;
  }

  .order-group{
    padding: 2em 1em;
    &__fields{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery-item{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
