.rating{
  display: flex;
  align-items: center;
  gap: 1em;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.75em;
    margin-right: .25em;
    padding: 0 .125em;
    svg {
      width: 100%;
      height: auto;
    }
    path {
      fill: transparent;
      stroke: $zheltyy;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: $zheltyy;
        stroke: $zheltyy;
      }
      &~.rating-input__item {
        path {
          fill: $zheltyy;
          stroke: $zheltyy;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: $zheltyy;
    stroke: $zheltyy;
  }
}

.reviews-slider,
.images-slider{
  &:before,
  &:after{
    content: '';
    position: absolute;
    top: 0;
    width: 5.5em;
    height: 100%;
    z-index: 2;
    pointer-events: none;
  }
  &:before{
    left: 0;
    background: linear-gradient(-270deg, #F8F3ED 0%, rgba(248, 243, 237, 0.00) 100%);
  }
  &:after{
    right: 0;
    background: linear-gradient(270deg, #F8F3ED 0%, rgba(248, 243, 237, 0.00) 100%);
  }
  &.first{
    &:before{
      opacity: 0;
    }
  }
  &.last{
    &:after{
      opacity: 0;
    }
  }
  .swiper-container{
    width: calc(100% + .75em);
  }
  .swiper-slide{
    padding-right: .75em;
  }
  .swiper-button{
    z-index: 3;
    width: 2.625em;
    height: 2.625em;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    path{
      fill: rgba($svetlo-korichnevyy,.4);
      stroke: $white;
    }
    &-prev{
      left: .125em;
    }
    &-next{
      right: .125em;
    }
    &.swiper-button-disabled{
      opacity: 0;
    }
  }
}

.images-slider{
   margin-top: 2em;
  .swiper-slide{
    width: 10.25em;
  }
  &__item{
    @include imgCover(125%);
    border-radius: 1em;
  }

}

.reviews-slider{
  margin-top: 1.125em;
  .swiper-slide{
    width: 25em;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: 1.5em;
    }
  }

  .images-slider,
  .reviews-slider{
    .slider__wrap{
      overflow: unset;
    }
    &:before,
    &:after,
    .swiper-button{
      display: none;
    }
    .swiper-container{
      width: calc(100% + .5em);
    }
    .swiper-slide{
      padding-right: .5em;
    }
  }

  .images-slider{
    margin-top: 1.75em;
    .swiper-slide{
      width: 10em;
    }
  }

  .reviews-slider{
    margin-top: 1em;
    .swiper-slide{
      width: 21em;
    }
  }
}


