.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 125%;
    margin-bottom: .375em;
    color: $svetlo-korichnevyy;
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.5em;
    border: 1px solid $svetlo-seryy;
    padding: 0 1.25em;
    height: 3.25em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $korichnevyy;
    font-weight: 500;
    &::placeholder {
      color: $svetlo-seryy;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 7.5em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    height: 3.25em;
    float: unset;
    &.disabled{
      color: $svetlo-seryy;
      pointer-events: none;
      &:before{
        background-image: url(../img/select-arrow2.svg);
      }
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: 1em;
      height: 1em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
      font-weight: 500;
    }
    .list{
      border-radius: 0.5em;
      box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
      width: calc(100% + 2px);
      left: -1px;
      border: none;
      background: #FFF;
      max-height: 25.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: unset;
      font-size: 1em;
      line-height: 125%;
      padding: .5em 1.5em;
      display: flex;
      align-items: center;
      color: $korichnevyy;
      font-weight: 500;
      border: none;
      border-bottom: 1px solid $svetlo-seryy;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $svetlo-seryy;
        border-color: $svetlo-seryy;
        font-weight: 500;
        color: $korichnevyy;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
  &._success{
    input{
      background-image: url(../img/success.svg);
      padding-right: 2.5em;
      background-size: 1.5em auto;
      background-repeat: no-repeat;
      background-position: right .75em center;
    }
  }
}

.fg-dynamic{
  label{
    position: absolute;
    top: 1em;
    left: 1.125em;
    padding: 0 .125em;
    background-color: $white;
    transition: all .2s;
  }
  input,textarea{
    &._filled,
    &:focus{
      &~label{
        top: -.625em;
      }
    }
  }
}

.file-input,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.25em;
    min-height: 1.5em;
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $svetlo-seryy;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check2.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  max-width: 100%;
  flex-shrink: 0;
  width: 7.5em;
  height: 3.5em;
  border-radius: 6.25em;
  border: 1px solid $svetlo-seryy;
  position: relative;
  input{
    height: 100%;
    width: 100%;
  }
  input{
    border: none;
    background-color: transparent;
    color: $korichnevyy;
    font-weight: 600;
    font-size: 1.25em;
    text-align: center;
  }
  svg{
    width: 100%;
    height: auto;
  }
  button{
    width: 1em;
    height: 1em;
    @include center;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    background-color: transparent;
  }
  .ccalc-minus{
    left: 1.5em;
  }
  .ccalc-plus{
    right: 1.5em;
  }
  ._disabled{
    path{
      stroke: $svetlo-seryy;
    }
  }
}


@media screen and (max-width: 992px){
  .cartcalc{
    width: 6.5em;
    .ccalc-minus{
      left: 1em;
    }
    .ccalc-plus{
      right: 1em;
    }
  }
}


