.review-card{
  padding: 2em;
  border-radius: 1.5em;
  height: 100%;
  background-color: $white;
  &__text{
    margin-top: .75em;
    .text18{
      @include truncate(4);
    }
  }
  &__more{
    &:hover{
      color: $korichnevyy;
    }
  }
}

._full{
  grid-column: 1/-1;
}

.review-form{
  padding: 3em 2.5em;
  border-radius: 1.5em;
  background-color: $white;
  width: 53.75em;
  max-width: 100%;
  margin: 0 auto;
  .rating{
    margin-top: 1em;
  }
  .rating-input__item{
    width: 3em;
    height: 3em;
    padding: 0 .1875em;
    &:not(:hover){
      path {
        stroke: $svetlo-seryy;
      }
    }
  }
  &__fields{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__btn{
    margin-top: 1em;
  }
}

.file{
  margin-top: 2em;
}

.file-input{
  margin-top: 1em;
  &__inner{
    @include center;
    min-height: 6.5em;
    padding: 2em 1em;
    border-radius: 0.5em;
    background: rgba(233, 211, 189, 0.24);
  }
  &__text{
    text-align: center;
    ._view2{
      display: none;
    }
  }
}

.gift-modal{
  &__grid{
    display: grid;
    grid-template-columns: 19em calc(100% - 22em);
    gap: 3em;
  }
  .product-sale{
    margin: 0;
    position: static;
    .sale__inner{
      min-height: 23em;
    }
    .sale__nav{
      display: none;
    }
    .sale__img{
      width: 100%;
      margin-top: 1.5em;
    }
  }
  &__fields{
    margin-top: .25em;
    @include grid100gap(1em);
  }
  &__nav{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .75em .5em;
  }
  &__btn{
    padding: 0;
  }
}


@media screen and (max-width: 992px){
  .review-card{
    padding: 1.5em 1.25em;
    border-radius: 1em;
    &__text{
      margin-top: .75em;
      .text18{
        @include truncate(4);
      }
    }
    &__more{
      &:hover{
        color: $korichnevyy;
      }
    }
  }

  .review-form{
    padding: 2em 1em;
    border-radius: 1em;
    max-width: unset;
    width: calc(100% + 2em);
    margin-left: -1em;
  }

  .gift-modal{
    &__fields{
      margin-top: 1.5em;
      gap: .75em;
    }
    &__nav{
      margin-top: 1.5em;
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .review-form{
    &__fields{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }

  .file-input{
    &__inner{
      min-height: 5.125em;
    }
    &__text{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .gift-modal {
    &__grid {
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
  }
}
