.catalog-item{
  &__inner{
    display: grid;
    grid-template-columns: 32.125em calc(100% - 34.625em);
    gap: 2.5em;
  }
  &__col2{
    position: relative;
  }
}

.gallery{
  position: sticky;
  top: 1em;
  display: grid;
  grid-template-columns: 5.5em calc(100% - 6.6875em);
  gap: 1.1875em;
  .swiper{
    height: calc(100% + .5em);
    &-slide{
      padding-bottom: .5em;
    }
    &-button{
      width: 2.625em;
      height: 2.625em;
      path{
        stroke: $white;
      }
    }
  }
  &__big,
  &__small{
    height: 34.5em;
    overflow: hidden;
    border-radius: .75em;
    position: relative;
  }
  &__big{
    .swiper-button{
      transform: translateY(-50%);
      &-prev{
        left: .5em;
      }
      &-next{
        right: .5em;
      }
    }
  }
  &__small{
    position: relative;
    .swiper-slide{
      height: 7.625em;
      cursor: pointer;
      &-thumb-active{
        .gallery__img:after{
          opacity: 1;
        }
      }
    }
    .gallery__img{
      border-radius: .75em;
      &:after{
        content: '';
        @include centerAbsolute;
        border-radius: inherit;
        width: 100%;
        height: 100%;
        border: 1px solid $krasnyy;
        opacity: 0;
      }
    }
    .swiper-button{
      left: 50%;
      width: 2em;
      height: 2em;
      transform: translateX(-50%);
      &.swiper-button-disabled{
        opacity: 0;
      }
      svg{
        transform: rotate(90deg);
      }
      &-prev{
        top: .5em;
      }
      &-next{
        right: unset;
        top: unset;
        bottom: .5em;
      }
    }
  }
  &__img{
    @include imgCover(0);
    border-radius: 1.5em;
    height: 100%;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__inner{
      grid-template-columns: 21.5em calc(100% - 22.75em);
      gap: 2em 1.25em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .5em;
    width: calc(100% + 2em);
    padding: 0 1em;
    margin-left: -1em;
    overflow: hidden;
    &__big{
      height: 29.0625em;
      overflow: unset;
    }
    &__img{
      border-radius: 1em;
    }
    .swiper{
      width: calc(100% + .5em);
      height: 100%;
    }
    .swiper-slide{
      padding: 0 .5em 0 0;
      width: 100%;
    }
    .swiper-button{
      display: none;
    }
    &__small{
      height: 7.14em;
      border-radius: unset;
      order: 2;
      margin-top: .5em;
      .swiper-slide{
        width: 6em;
        height: 7.14em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      display: flex;
      flex-direction: column;
    }
  }

  .gallery{
    &__small{
      overflow: unset;
      .swiper{
        overflow: unset;
      }
    }
  }
}
