.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 57em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 4.5em 4em;
        border-radius: 1.5em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 2em;
        right: 2em;
    }
}

.close-btn{
    width: 2.5em;
    @include center;
    flex-shrink: 0;
    background-color: transparent;
    img{
        width: 100%;
    }
}

.success-modal{
    .modal__dialog{
        width: 37.5em;
        padding: 3.5em 4em;
        text-align: center;
    }
    &__icon{
        width: 9.5em;
        display: block;
        margin: 0 auto 1.5em;
    }
    &__nav{
        margin-top: 2.5em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 4.5em 1em 2em;
            border-radius: 1em;
            overflow: hidden;
        }
        .close-btn{
            top: 1em;
            right: 1em;
        }
    }

    .success-modal{
        .modal__dialog{
            padding: 4.5em 1em 2em;
        }
        &__icon{
            width: 5em;
            margin-bottom: .5em;
        }
        &__nav{
            margin-top: 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
