.footer{
  margin-top: 3.75em;
  padding: 2.5em 0;
  background: #233318;
  color: $white;
  &__top{
    display: grid;
    grid-template-columns: 25em calc(100% - 29em);
    gap: 4em;
  }
  &__logo{
    width: 13em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    margin-top: 1.25em;
  }
  &__bottom{
    display: flex;
    justify-content: flex-end;
    gap: 1.5em;
    margin-top: 1.81em;
  }
}

.footer-info{
  margin-top: 2em;
  @include grid100gap(.5em);
}

.footer-info2{
  margin-top: 2.25em;
  @include grid100gap(.5em);
}

.footer-wrap{
  display: flex;
  justify-content: space-between;
  padding-left: 2.625em;
}

.footer-nav{
  font-weight: 500;
  ul{
    @include grid100gap(.5em);
  }
}

.footer-contacts{
  min-width: 17.5em;
  &__items{
    @include grid100gap(1em);
  }
}

.footer-consult{
  border-radius: 1.5em;
  background: linear-gradient(89deg, #6A8856 0.58%, rgba(106, 136, 86, 0.00) 127.24%);
  backdrop-filter: blur(3.4447367191314697px);
  padding: .75em 2.5em;
  margin-top: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content{
    @include grid100gap(.25em);
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 2.5em;
    padding: 2em 0;
    &__top{
      grid-template-columns: 18em calc(100% - 20em);
      gap: 2em;
    }
    &__logo{
      width: 11.125em;
    }
    &__bottom{
      justify-content: unset;
      gap: .25em 1em;
      margin-top: 1.5em;
    }
  }

  .footer-info{
    font-weight: 400;
  }

  .footer-info2{
    margin-top: 1.5em;
  }

  .footer-wrap{
    padding: 0;
  }

  .footer-contacts{
    min-width: unset;
    &__items{
      gap: .5em;
    }
  }

  .footer-consult{
    padding: 2em .75em 1.75em;
    display: block;
    &__content{
      gap: .5em;
      padding: 0 1.25em;
    }
    &__btn{
      padding: 0;
      width: 100%;
      height: 3.25em;
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      position: relative;
      padding-top: 3.25em;
      grid-template-columns: 100%;
      &-col{
        order: 2;
      }
      &-col2{
        position: relative;
        padding-bottom: 4em;
      }
    }
    &__logo{
      position: absolute;
      top: 0;
      left: 0;
    }
    .socials{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      justify-content: center;
    }
    &__bottom{
      flex-wrap: wrap;
    }
  }

  .footer-info{
    margin: 0;
  }

  .footer-wrap{
    display: block;
  }

  .footer-contacts{
    margin-top: 1.5em;
  }
}
