.product-sale{
  width: 19em;
  position: absolute;
  right: 0;
  top: 14.125em;
  @media screen and (min-width: 577px){
    .sale{
      &__inner{
        border-radius: 1em;
        padding: 1em 1.5em 1em;
        min-height: 21em;
      }
      &__inner{
        background-image: url(../img/sale-bg2.webp);
        padding-right: .75em;
      }
      &__top{
        display: none;
      }
      &__subtitle{
        .h4{
          font-size: .96em;
        }
      }
      &__img{
        position: static;
        width: 13.72em;
        margin: .33em auto 0;
        display: block;
      }
      &__nav{
        margin-top: 0;
        justify-content: center;
      }
      &__link{
        padding: 0 2.5em;
        height: 3em;
        span{
          font-size: 1.125em;
        }
      }
    }
    .sale-gift{
      margin-top: .375em;
      margin-left: -.875em;
      &__row{
        padding: .4em .75em;
        border-radius: 1.43em 1.01em 1.01em 0;
      }
      &__row2{
        margin-top: -.3em;
        padding: 0 1.16em .31em .75em;
        border-radius: 0 0 1.43em 1.43em;
      }
      &__title{
        font-size: 2.1875em;
        line-height: 99%;
      }
      .h4{
        font-size: .96em;
      }
    }
  }
}

.product-cart{
  width: 19em;
  position: absolute;
  right: 0;
  top: 0;
  border-radius: 1.5em;
  padding: 1.25em 1em;
  background-color: $white;
  &__prices{
    display: flex;
    align-items: flex-end;
    gap: 1.5em;
  }
  &__price2{
    font-weight: 500;
    text-decoration: line-through;
  }
  .product-card__bottom{
    padding-top: 1em;
  }
  .cartcalc{
    width: 6.5em;
    .ccalc-minus{
      left: 1em;
    }
    .ccalc-plus{
      right: 1em;
    }
  }
  &__btn{
    width: 100%;
    margin-top: .5em;
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .product-sale{
    position: static;
    width: 100%;
    margin-top: .75em;
  }

  .product-cart{
    margin-top: 2em;
    width: 100%;
    position: static;
    border-radius: 1em;
    &__btn{
      span{
        font-size: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .product-cart{
    order: -1;
    margin: 0 0 2em;
  }
}
