.header{
  position: relative;
  z-index: 10;
  &__logo{
    display: block;
    flex-shrink: 0;
    img{
      width: auto;
      height: 2.5em;
      display: block;
    }
  }
}

.header-top{
  &__inner{
    padding: .75em 0;
  }
}

.header-contacts{
  &__show{
    width: 1.5em;
    @include center;
    display: none;
    img{
      width: 100%;
    }
  }
  &__hidden{
    display: flex;
    align-items: center;
  }
  &__info{
    margin-right: 24.375em;
  }
  &__links{
    @include grid100gap(.25em);
  }
  &__link{
    display: inline-block;
    margin-right: auto;
  }
  .socials{
    display: none;
  }
}

.burger{
  display: none;
}

.header-bottom{
  border-top: 1px solid $svetlo-seryy;
  &__inner{
    padding: .625em 0;
    position: relative;
  }
}

.header-catalog{
  position: relative;
  &__show{
    font-weight: 600;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0 1.5em;
    height: 2.75em;
    border-radius: 5em;
    background: linear-gradient(90deg, #9F5510 -8.51%, #F7D46B 102.13%);
    color: $white;
    img{
      width: 1.25em;
      margin-right: .75em;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .25em;
    width: 21.5em;
    border-radius: 0.75em;
    background-color: $white;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
    padding: .75em 0;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__link{
    padding: 1.375em 1.5em;
    display: flex;
    align-items: center;
    border-bottom: 1px solid $svetlo-seryy;
    font-weight: 500;
    &:last-child{
      border-bottom: none;
    }
  }
  &._open{
    .header-catalog__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      margin-right: auto;
      img{
        height: 1.5625em;
      }
    }
  }

  .header-top{
    &__inner{
      justify-content: flex-end;
      padding: .5em 0;
    }
  }

  .header-contacts{
    position: relative;
    &._open{
      .header-contacts__show img{
        transform: rotate(-180deg);
      }
      .header-contacts__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      display: flex;
      img{
        transition: all .3s;
      }
    }
    &__hidden{
      flex-direction: column;
      align-items: flex-start;
      border-radius: 0.75em;
      background-color: $white;
      box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
      position: absolute;
      top: 100%;
      right: -3em;
      width: 21.5em;
      padding: 1em 0;
      margin-top: .5em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      z-index: 3;
    }
    &__info{
      margin: 0;
      padding: .75em 1.5em;
      @include grid100gap(.25em);
    }
    &__links{
      width: 100%;
      gap: .5em;
      order: -1;
      padding: 1em 1.5em;
      border-bottom: 1px solid rgba(233, 211, 189, 0.40);
    }
    .socials{
      display: flex;
      padding: .75em 1.5em;
    }
  }

  .burger{
    width: 1.5em;
    @include center;
    margin-left: 1.5em;
    img{
      width: 100%;
    }
  }

  .header-bottom{
    &__inner{
      padding: .5em 0;
    }
  }
}


