@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    border-radius: 5em;
    height: 3.5em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-pink{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-pink-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-brown{
    background-color: $korichnevyy;
    color: $white;
    &:hover{
        background-color: darken($korichnevyy,10%);
        color: $white;
    }
}
