.pack{
  &__bg{
    position: absolute;
    right: -5.625em;
    top: -7.375em;
    width: 52.8125em;
    pointer-events: none;
  }
  &__bg2{
    display: none;
    pointer-events: none;
  }
  &__inner{
    position: relative;
  }
  &__content{
    max-width: 37.5em;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 37.5em;
    width: 37em;
  }
  &__items{
    margin-top: 1.75em;
    @include grid100gap(2em);
    max-width: 25em;
  }
  @media screen and (min-width: 993px){
    .advantage-item .text18{
      font-size: 1.75em;
      line-height: 114%;
    }
  }
}

.main-banner{
  position: relative;
  @media screen and (min-width: 993px){
    padding-bottom: 40.5%;
    &__wrap{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
    }
  }
  &__inner{
    min-height: 36.5em;
    padding: 6.44em 0 5.375em;
    position: relative;
  }
  &__img{
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include imgCover(0);
    position: absolute;
    ._view2{
      display: none;
    }
  }
  &__subtitle{
    font-size: 1.125em;
    line-height: 111%;
    text-transform: uppercase;
    font-weight: 500;
  }
  &__title{
    margin: 1em 0 .5em;
    max-width: 40.25em;
  }
  &__badge{
    display: inline-flex;
    align-items: center;
    height: 2.625em;
    padding: 0 1.5em;
    border-radius: 1.5em;
    background: linear-gradient(90deg, #9F5510 -8.51%, #F7D46B 102.13%);
    color: $white;
  }
  &__badge2{
    position: absolute;
    right: 16.5em;
    top: 12.5em;
    border-radius: 2.5em;
    background: linear-gradient(90deg, #D63A6C 0%, #FFBAD8 100%);
    box-shadow: 0 7px 26.8px 0 rgba(134, 110, 79, 0.46);
    padding: 0 1.5em;
    display: flex;
    align-items: center;
    color: $white;
    height: 2.5em;
  }
  &__bottom{
    margin-top: 5.375em;
  }
  &__btn{
    width: 17.5em;
    span{
      font-size: 1.75em;
    }
  }
  &__info{
    @include grid100gap(.5em);
    margin-left: 1.5em;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .pack{
    &__bg{
      right: -1em;
      width: 30em;
      top: -1.625em;
    }
    &__content{
      max-width: 21.5em;
    }
    &__img{
      right: 0;
      left: unset;
      width: 21em;
    }
  }

  .main-banner{
    &__inner{
      min-height: 19em;
      padding: 1.625em 0;
    }
    &__subtitle{
      font-size: .875em;
      line-height: 143%;
    }
    &__title{
      margin: .5em 0;
      max-width: 21.25em;
    }
    &__badge{
      height: 2.375em;
    }
    &__badge2{
      display: none;
    }
    &__bottom{
      margin-top: 2em;
    }
    &__btn{
      height: 3.25em;
      span{
        font-size: 1.375em;
      }
    }
    &__info{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .pack{
    &__bg{
      display: none;
    }
    &__bg2{
      position: absolute;
      display: block;
      right: -1em;
      width: calc(100% + 2em);
      top: -1.625em;
    }
    &__img{
      position: static;
      display: block;
      margin-top: 2em;
      margin-left: -.25em;
      width: calc(100% + 1.25em);
    }
    .advantage-item{
      display: flex;
      align-items: flex-start;
      &__icon{
        margin: 0 .75em 0 0;
      }
      &__text{
        max-width: 16.5em;
        min-height: 3em;
        display: flex;
        align-items: center;
      }
    }
  }

  .main-banner{
    &__inner{
      min-height: 33em;
      position: static;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__bottom{
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      padding: 2em 1em;
    }
    &__btn{
      width: 100%;
    }
  }
}
