.delivery-info{
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 19em 19em 19em;
    justify-content: space-between;
  }
}

.delivery-item2{
  &__title{
    margin-top: .5em;
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(.5em);
    &-item{
      font-weight: 500;
      font-size: 1em;
      line-height: 125%;
    }
  }
}

.pay-info{
  &__grid{
    display: grid;
    grid-template-columns: 30em calc(100% - 35.5em);
    gap: 5.5em;
    margin-top: 1.875em;
  }
}

.pay-item{
  padding: 2.5em;
  border-radius: 1.5em;
  background-color: $white;
  &__icon{
    height: 4em;
    margin-bottom: 1.25em;
    display: block;
  }
}

.contacts{
  &__grid{
    display: grid;
    grid-template-columns: 25em 39em;
    justify-content: space-between;
    margin-top: 2.5em;
  }
  &__items{
    margin-top: 2em;
    @include grid100gap(1.5em);
  }
  &__item{
    @include grid100gap(.5em);
    font-weight: 500;
  }
}

.sell-department{
  padding: 3.5em 4.5em;
  background-color: $white;
  border-radius: 1.5em;
  &__links{
    margin-top: 2em;
    @include grid100gap(1em);
  }
  &__link{
    font-weight: 500;
    display: inline-block;
    margin-right: auto;
  }
  &__bottom{
    margin-top: 3.25em;
  }
  &__subtitle{
    font-size: 2em;
    line-height: 113%;
    text-transform: uppercase;
    font-family: $font2;
    font-weight: 700;
    color: $primary;
  }
}

.contacts-form{
  margin-top: 7.5em;
}


@media screen and (max-width: 992px){
  .delivery-info{
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }

  .pay-info{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1em 2em;
      margin-top: 2em;
    }
  }

  .pay-item{
    padding: 2em 1.5em;
  }

  .contacts{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 5em 1.5em;
      margin-top: 2em;
    }
    &__items{
      margin-top: 1em;
    }
  }

  .sell-department{
    padding: 2.5em 1.5em;
    &__links{
      margin-top: 1.5em;
      gap: .5em;
    }
    &__bottom{
      margin-top: 2.5em;
    }
    &__subtitle{
      font-size: 1.5em;
      line-height: 117%;
    }
  }

  .contacts-form{
    margin-top: 5em;
  }
}


@media screen and (max-width: 576px){
  .contacts,
  .pay-info,
  .delivery-info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contacts-form{

  }
}
