.main-time{
  position: absolute;
  right: 2.375em;
  bottom: 1.56em;
  border-radius: 5em;
  border: 1px solid rgba(255, 255, 255, 0.72);
  background: linear-gradient(87deg, rgba(131, 97, 64, 0.58) 5.55%, rgba(131, 97, 64, 0.23) 80.15%);
  backdrop-filter: blur(3.4447367191314697px);
  padding: .5em 1em .5em 7.25em;
  display: flex;
  align-items: center;
  gap: 1.25em;
  font-weight: 600;
  color: $white;
  &__item{
    display: flex;
    align-items: center;
    &:nth-child(1){
      align-self: flex-end;
      align-items: flex-end;
      padding-bottom: .4em;
    }
  }
  &__num,
  &__num2{
    font-weight: 700;
    font-family: $font3;
    text-transform: uppercase;
  }
  &__num{
    position: absolute;
    bottom: .69em;
    left: 1.77em;
    span{
      font-size: 6.15131em;
      line-height: 50%;
    }
  }
  &__num2{
    margin-right: .5em;
    span{
      font-size: 2.95263em;
      line-height: 104%;
    }
  }
}

.main-bottom{
  margin-top: 6.5em;
  &__inner{
    position: relative;
    padding: 0 4.5em 2.5em 8.25em;
    border-radius: 1.5em;
    background-color: $white;
    display: grid;
    grid-template-columns: 14em 21em 18em;
    justify-content: space-between;
  }
  @media screen and (min-width: 993px){
    .advantage-item {
      margin-top: -2.81em;
      &__icon{
        height: 5em;
      }
      .text18{
        font-size: 1.25em;
        line-height: 120%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main-time{
    display: none;
  }

  .main-bottom{
    margin-top: 5em;
    &__inner{
      padding: 0;
      border-radius: 0;
      background-color: transparent;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 2.5em 1.5em;
    }
    .advantage-item {
      &__icon{
        height: 4.5em;
      }
    }
  }

}


@media screen and (max-width: 576px){
  .main-bottom{
    &__inner{
      grid-template-columns: 100%;
      width: 17.5em;
      margin: 0 auto;
    }
    .advantage-item {
      text-align: center;
      &__icon{
        margin: 0 auto 1em;
      }
    }
  }
}
