.set{
  position: relative;
  &__grid{
    display: grid;
    grid-template-columns: 32em calc(100% - 33.5em);
    gap: 2em 1.5em;
    margin-top: 2.625em;
  }
  .h4{
    display: none;
  }
  &__img{
    width: 100%;
    left: 0;
    bottom: -8.75em;
    position: absolute;
    pointer-events: none;
  }
}

.set-info,
.set-video{
  &__img{
    height: 51.5em;
    border-radius: 1.5em;
    @include imgCover(0);
  }
}

.set-info{
  position: relative;
  &__img{
    ._view2{
      display: none;
    }
  }
}

.set-point{
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  &:first-child{
    top: 3.125em;
    left: 3.5em;
  }
  &:nth-child(2){
    right: 3.69em;
    top: 6.56em;
  }
  &:nth-child(2),
  &:nth-child(5){
    flex-direction: column-reverse;
    &:before{
      background-image: url(../img/line2.svg);
    }
  }
  &:nth-child(3){
    left: 1.19em;
    top: 16.125em;
  }
  &:nth-child(4){
    right: 2.625em;
    bottom: 9em;
  }
  &:nth-child(5){
    left: 2.69em;
    bottom: 7em;
  }
  &__text{
    padding: 0 1.5em;
    height: 2.75em;
    border-radius: 5em;
    background: linear-gradient(85deg, #FFF -2.57%, rgba(255, 255, 255, 0.00) 122.49%);
    backdrop-filter: blur(4.099999904632568px);
    display: flex;
    align-items: center;
    font-weight: 600;
    order: -1;
  }
  &:before,
  &:after{
    content: '';
    @include bgImgSize(100%);
  }
  &:before{
    width: .125em;
    height: 3em;
    background-image: url(../img/line.svg);
  }
  &:after{
    width: 2.625em;
    height: 2.625em;
    background-image: url(../img/plus.svg);
  }
}


@media screen and (max-width: 992px){
  .set{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      margin-top: 2em;
    }
    &__img{
      display: none;
    }
  }

  .set-info,
  .set-video{
    &__img{
      height: unset;
      border-radius: .83em;
      padding-bottom: 177%;
    }
  }

  .set-info{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .set-point{
    &:nth-child(4){
      flex-direction: column-reverse;
      &:before{
        background-image: url(../img/line2.svg);
      }
    }
    &:first-child{
      top: 1em;
      left: .75em;
    }
    &:nth-child(2){
      right: .75em;
      top: 5em;
    }
    &:nth-child(3){
      left: unset;
      right: .75em;
      top: 14.125em;
    }
    &:nth-child(4){
      right: .75em;
      bottom: 1.75em;
    }
    &:nth-child(5){
      left: .75em;
      bottom: 9.5em;
    }
    &__text{
      padding: 0 1em;
      height: 2em;
    }
    &:before{
      width: .0625em;
      height: 2em;
    }
    &:after{
      width: 1.5em;
      height: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .set{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      order: 2;
    }
    .h4{
      display: block;
    }
  }

  .set-video{
    margin-top: 1em;
  }
}
