@import "../vars.scss";
.breadcrumbs {
    padding: 1.1875em 0;
    margin-bottom: 1em;
    color: $svetlo-korichnevyy;
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                top: .125em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 157%;
            }
        }
        &:last-child{
            pointer-events: none;
            color: $korichnevyy;
            &:before{
                color: $svetlo-seryy;
            }
        }
    }
    a,span{
        font-size: .875em;
        line-height: 157%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0;
        margin: 0;
    }
}


