._no-wrap{
  white-space: nowrap;
}

.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h1,.h2{
  font-family: $font2;
  text-transform: uppercase;
  font-weight: 700;
}

.h1{
  font-size: 5em;
  line-height: 105%;
}

.h2{
  font-size: 3.5em;
  line-height: 107%;
}

.h3{
  font-size: 2em;
  line-height: 113%;
  font-weight: 600;
}

.h4{
  font-size: 1.75em;
  line-height: 115%;
  font-weight: 600;
}

.h5{
  font-size: 1.25em;
  line-height: 120%;
  font-weight: 600;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 122%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.footer-nav a,
.text16{
  font-size: 1em;
  line-height: 125%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 128%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 150%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.pink-text{
  color: $primary;
}

.green-text{
  color: $zelenyy;
}

.brown-light-text{
  color: $svetlo-korichnevyy;
}

.red-text{
  color: $krasnyy;
}

.lh150{
  line-height: 150%;
}

.m-price{
  font-size: 2.25em;
  font-weight: 700;
}




@media screen and (max-width: 992px){
  .m-price{
    font-size: 1.875em;
    line-height: 107%;
  }

  .h1{
    font-size: 2.5em;
    line-height: 110%;
  }

  .h2{
    font-size: 2em;
    line-height: 113%;
  }

  .h3{
    font-size: 1.75em;
  }

  .h5{
    font-size: 1.125em;
    line-height: 122%;
  }

  .h4,
  .h4-mob{
    font-size: 1.375em;
  }

  .h5-tablet{
    font-size: 1.125em;
    line-height: 122%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
    line-height: 125%;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
    line-height: 128%;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
    line-height: 150%;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
